html {
  box-sizing: border-box; }

*,
::before,
::after {
  box-sizing: inherit; }

.content-box-component {
  box-sizing: content-box; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: rect(0, 0, 0, 0); }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.4em;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  background-color: #ffffff; }

.container {
  width: 1200px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px; }

@media (max-width: 1199px) {
  .container {
    width: 768px;
    padding-right: 45px;
    padding-left: 45px; } }

@media (max-width: 767px) {
  .container {
    width: 320px;
    padding-right: 15px;
    padding-left: 15px; } }

.logo {
  width: 98px;
  height: 20px; }

@media (max-width: 1199px) {
  .logo {
    width: 82px;
    height: 17px; } }

@media (max-width: 767px) {
  .logo {
    width: 68px;
    height: 15px; } }

.main-header {
  display: flex;
  width: 1200px;
  margin: 0 auto;
  padding-top: 31px;
  padding-right: 20px;
  padding-left: 20px;
  justify-content: space-between; }

.main-header__link {
  font-size: 26px;
  line-height: 1em;
  font-weight: 700;
  letter-spacing: 0.03em;
  font-family: "Bebas Neue", "Arial", sans-serif;
  color: #ffffff;
  text-decoration: none; }

@media (max-width: 1199px) {
  .main-header {
    width: 768px;
    padding-top: 41px;
    padding-right: 45px;
    padding-left: 45px; }
  .main-header__link {
    font-size: 24px;
    letter-spacing: 0.02em; } }

@media (max-width: 767px) {
  .main-header {
    width: 320px;
    padding-top: 18px;
    padding-right: 15px;
    padding-left: 15px; }
  .main-header__link {
    font-size: 20px; } }

.hero {
  position: relative;
  color: #ffffff; }

.hero__wrapper {
  position: relative;
  display: flex;
  width: 460px;
  padding-top: 111px;
  padding-right: 64px;
  padding-bottom: 43px;
  flex-direction: column;
  align-items: flex-end;
  text-align: right; }

.hero__wrapper::after {
  content: "";
  position: absolute;
  top: -6px;
  right: -500px;
  width: 500px;
  height: 556px;
  background-color: #ffffff;
  background-image: url("../img/superman-desktop@1x.jpg");
  background-repeat: no-repeat; }

.hero__title {
  margin: 0;
  margin-bottom: 51px;
  padding: 0;
  font-size: 109px;
  line-height: 1em;
  letter-spacing: 0.03em;
  font-weight: 700;
  font-family: "Bebas Neue", "Arial", sans-serif;
  text-shadow: -2px -2px #ffffff, 2px -2px #ffffff, -2px 2px #ffffff, 2px 2px #ffffff; }

.hero__city {
  margin: 0;
  margin-bottom: 17px;
  padding: 0;
  order: -1;
  font-size: 49px;
  line-height: 1em;
  letter-spacing: 0.03em;
  font-weight: 400;
  font-family: "Bebas Neue", "Arial", sans-serif; }

.hero__list {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 400;
  list-style: none; }

.hero__services {
  position: absolute;
  left: 21px;
  top: 298px;
  max-width: 120px; }

.hero__btn {
  display: flex;
  width: 232px;
  padding-top: 21px;
  padding-bottom: 20px;
  justify-content: center;
  font-size: 12px;
  line-height: 1em;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 4px solid #ffffff;
  text-decoration: none; }
  .hero__btn:hover, .hero__btn:focus {
    opacity: 0.5; }

.hero__social {
  display: none; }

@media (min-width: 1200px) {
  .hero__social {
    position: absolute;
    right: 17px;
    bottom: 23px;
    display: flex;
    flex-direction: column; }
  .hero__link {
    display: flex;
    margin-bottom: 21px;
    font-size: 12px;
    line-height: 1em;
    text-transform: uppercase;
    color: #ffffff;
    text-decoration: none; }
    .hero__link:hover, .hero__link:focus {
      opacity: 0.5; } }

@media (max-width: 1199px) {
  .hero__title {
    margin-bottom: 36px;
    font-size: 86px; }
  .hero__city {
    margin-bottom: 10px;
    font-size: 34px; }
  .hero__wrapper {
    width: 354px;
    padding-top: 71px;
    padding-right: 30px; }
  .hero__wrapper::after {
    top: 17px;
    right: -324px;
    width: 324px;
    height: 390px;
    background-image: url("../img/superman-tablet@1x.jpg"); }
  .hero__services {
    left: 0;
    top: 202px; }
  .hero__btn {
    width: 183px; } }

@media (max-width: 767px) {
  .hero__wrapper {
    width: 100%;
    padding-top: 73px;
    padding-right: 0; }
  .hero__wrapper::after {
    display: none; }
  .hero__title {
    margin-bottom: 38px;
    font-size: 78px; }
  .hero__city {
    margin-bottom: 15px;
    font-size: 28px; }
  .hero__services {
    top: 192px; }
  .hero__btn {
    width: 100%;
    padding-bottom: 22px; } }

.header-wrapper {
  min-height: 658px;
  color: #ffffff;
  background: radial-gradient(circle at 6% 93%, #003d85, #b80027 50%); }

@media (max-width: 1199px) {
  .header-wrapper {
    min-height: 554px; } }

@media (max-width: 767px) {
  .header-wrapper {
    min-height: 491px; } }
